<template>
	<view>
		<u-modal v-model="showAuthorizeUser" :show-title="false" :show-confirm-button="false">
			<view class="slot-content">
				<div class="auth-card">
					<div class="img">
						<img
							class="avatar-img"
							src="https://mmbiz.qpic.cn/mmbiz/icTdbqWNOwNRna42FI242Lcia07jQodd2FJGIYQfG0LAJGFxM4FbnQP6yfMxBgJ0F3YRqJCJ1aPAK2dQagdusBZg/0"
							mode="widthFix"
						/>
					</div>
					<div class="title">{{ bname }}</div>
					<div class="content">邀请您补全个人信息 （昵称、头像）</div>
					<div style="margin-left: 100rpx;margin-right: 100rpx">
						<u-form :model="user" ref="uForm">
							<u-form-item label="头像">
								<button class="avatar-wrapper" open-type="chooseAvatar" @chooseavatar="onChooseAvatar" slot="right">
									<image class="avatar" :src="user.avatarUrl"></image>
								</button>
							</u-form-item>
							<u-form-item label="昵称">
								<input type="nickname" class="weui-input" @blur="userNameInput" placeholder="请输入昵称" />
							</u-form-item>
						</u-form>
					</div>
				</div>
				<div class="auth-btncard">
					<div class="btn-unok">
						<u-button :hair-line="false" :custom-style="customStyleUnOk" @click="showAuthorizeUser = false">拒绝</u-button>
					</div>
					<div class="btn-ok"><u-button :hair-line="false" :custom-style="customStyleOk" @click="authUser">允许</u-button></div>
				</div>
			</view>
		</u-modal>
	</view>
</template>

<script>
export default {
	data() {
		return {
			user: {
				avatarUrl: 'https://mmbiz.qpic.cn/mmbiz/icTdbqWNOwNRna42FI242Lcia07jQodd2FJGIYQfG0LAJGFxM4FbnQP6yfMxBgJ0F3YRqJCJ1aPAK2dQagdusBZg/0',
				nickName: ''
			},
			// 是否弹出登录注册授权弹窗
			showAuthorizeUser: false
		};
	},
	methods: {
		//获取昵称输入内容
		userNameInput(e) {
			this.user.nickName = e.detail.value;
		},
		onChooseAvatar(e) {
			console.log('头像信息》');
			console.log(e);
			this.user.avatarUrl = e.detail.avatarUrl;
		},
		authUser() {
			let userInfo = uni.getStorageSync('userInfo');
			if (this.user.nickName == '') {
				this.$interactive.toast('请输入您的昵称！');
				return;
			}
			userInfo.avatarurl = this.user.avatarUrl;
			userInfo.nickname = this.user.nickName;
			this.userInfo = userInfo;
			uni.setStorageSync('userInfo', userInfo);
			this.user.id = userInfo.id;
			this.$api.saveUserInfo(
				this.user,
				res => {
					this.hasUserInfo = true;
					this.showAuthorizeUser = false;
				},
				res => {}
			);
		}
	}
};
</script>

<style lang="less" scoped>
.auth-btncard {
	.btn-unok {
		width: 50%;
		float: left;
	}
	.btn-ok {
		width: 50%;
		float: left;
		margin: 0;
		padding: 0;
		border: 0px solid transparent; //自定义边框
		outline: none; //消除默认点击蓝色边框效果
		u-button {
			margin: 0;
			padding: 0;
			border: 0px solid transparent; //自定义边框
			outline: none; //消除默认点击蓝色边框效果
		}
	}
}
.auth-card {
	text-align: center;
	.avatar-img {
		width: 150rpx;
		height: 150rpx;
		overflow: hidden;
		border-radius: 100%;
		margin-top: 30rpx;
	}
	.title {
		font-size: 20rpx;
	}
	.content {
		margin-top: 10rpx;
	}
}
.mine_box {
	height: 360upx;
	color: #fff;
	background-color: pink;
	.avatar-img {
		width: 150rpx;
		height: 150rpx;
		overflow: hidden;
		border-radius: 100%;
		/*margin-right: 40rpx;*/
	}
	.avatar_box {
		position: relative;
		width: 150rpx;
		height: 150rpx;
		overflow: hidden;
		border-radius: 100%;
		margin-right: 40rpx;
		.avatar-img {
			width: 100%;
			height: 100%;
			/* #ifdef MP-WEIXIN*/
			width: 150rpx;
			height: 150rpx;
			/* #endif */
		}
		.get_user_data {
			z-index: 11;
			opacity: 0;
			width: 100%;
			height: 100%;
			position: absolute;
			left: 0;
			top: 0;
		}
	}

	.nick-name-box {
		height: 100%;
	}
	.user-nickName {
		font-size: 40rpx;
		color: #fff;
		margin-bottom: 20rpx;
	}
	.shiming_box {
		border-radius: 10upx;
		font-size: 28upx;
		padding: 10upx 20upx;
		background-color: rgba(15, 77, 139, 0.5);
		.icon-shezhi {
			margin-right: 10rpx;
			margin-top: 5rpx;
		}
	}
}
.authorize-box {
	background-color: transparent;
	text-align: center;
	.authorize-avatar {
		width: 200rpx;
		height: 200rpx;
	}
	.authorize-btn {
		position: relative;
		top: 40rpx;
	}
}
/deep/.u-mode-center-box {
	background-color: transparent !important;
}
//-----------------
.avatar-wrapper {
	width: 150rpx;
	height: 100rpx;
	color: #333 !important;
	text-align: center !important;
	border: none !important;
	border-radius: 0 !important;
	background-color: transparent !important;
}
.avatar-wrapper::after {
	border: none !important;
}
.avatar {
	width: 100rpx;
	height: 100rpx;
	overflow: hidden;
	border-radius: 100%;
}
</style>
